%menu-text {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 13px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;

  transition: color $transitionTime;
}

%special-title {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #c0cdd7;
}

%section-title {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
}

%button-style {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
  text-align: center;
  color: #fff;

  border-radius: 8px;

  transition: background-color $transitionTime;

  cursor: pointer;
}

%style-icon {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;
}
